import {
    Directive,
    ElementRef,
    Input,
    OnDestroy,
    Optional,
    Renderer
} from '@angular/core';

import { GridComponent } from './grid.component';

/**
 * @hidden
 */
@Directive({
    selector: '[kendoGridResizableContainer]'
})
export class ResizableContainerDirective implements OnDestroy {

    @Input('lockedWidth') public lockedWidth: number;
    @Input() public set kendoGridResizableContainer(enabled: boolean) {
        const refresh = enabled !== this.enabled;
        this.enabled = enabled;
        if (refresh) {
            this.attachResize();
            this.resize();
        }
    }

    private enabled: boolean = false;
    private resizeSubscription: Function;

    constructor(
        private el: ElementRef,
        private renderer: Renderer,
        @Optional() private grid?: GridComponent
    ) { }

    public ngOnDestroy(): void {
        if (this.resizeSubscription) {
            this.resizeSubscription();
        }
    }

    private attachResize(): void {
        if (this.resizeSubscription && !this.enabled) {
            this.resizeSubscription();
            this.resizeSubscription = null;
        }

        if (!this.resizeSubscription && this.enabled) {
            this.resizeSubscription = this.renderer.listenGlobal('window', 'resize', this.resize.bind(this));
        }
    }

    private resize(): void {
        if (this.grid && this.grid.wrapper) {
            const containerElement: HTMLElement = this.grid.wrapper.nativeElement;
            const width = this.enabled ? Math.max(containerElement.clientWidth - this.lockedWidth, 0) + "px" : "";
            this.el.nativeElement.style.width = width;
        }
    }
}



